.UserDialog-w{
	/*全屏*/
	position:fixed;
	top:0;
	bottom:0;
	left:0;
	right:0;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack:center;
	    justify-content:center;
	-ms-flex-align:center;
	    align-items:center;
	background:rgba(0,0,0,.3);
	background:url(./bg1.jpg) center no-repeat;
	background-size: cover;
}
.UserDialog{
	border:1px solid #aaa;
	border-radius: 10px;
	overflow: hidden;
	min-width: 20em;
	background:#7B7DEE;
	color:#fff;
	-webkit-box-shadow: 0 0 1em hsla(0,0,0%,0.35);
	        box-shadow: 0 0 1em hsla(0,0,0%,0.35)
}
.UserDialog .signInOrSignUp>nav:first-child{
	padding:1em;
}
.UserDialog .signInOrSignUp>nav:first-child label{
	cursor:pointer;
	font-size:20px;
	font-weight: bold;
}
.UserDialog .signInOrSignUp>.panels{
	padding:0 1em 1em;
}
.UserDialog form>.row{
	display:-ms-flexbox;
	display:flex;
	padding:.3em 0;
}
.UserDialog form>.row>label:first-child{
	width:4em;
}
.UserDialog form>.row.actions{
	padding-left:4em;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-align:center;
	    align-items:center;
}
.UserDialog form>.row.actions button{
	margin-right:.5em;
}
.UserDialog button{
	font-:inherit;
	padding:.2em;
}
.UserDialog .forgotPassword h3{
	padding:1em;
}
.UserDialog .forgotPassword > form {
	padding:0 1em 1em;
}
.UserDialog .forgotPassword > form button{
	padding:.5em;
	outline: none;
	border-radius: 4px;
	overflow: hidden;
	background:#1296db;
	color:#fff;
	cursor:pointer;
}


